import React from "react"

import { Link, graphql } from "gatsby"
import Img from "gatsby-image"

import RelativeTime from "react-relative-time"

import Layout from "../components/Layout"
import Meta from "../components/Meta"
import PageHeader from "../components/PageHeader"

import { FaFacebook, FaInstagram, FaGithub, FaLinkedin, FaTwitter } from "react-icons/fa"

const IndexPage = ({ data }) => {
    return (
        <Layout>
            <Meta title="Home" />
            <PageHeader
                title={data.site.siteMetadata.title}
                subtitle={data.site.siteMetadata.description}
                typewriter={true}
                alternatives={data.site.siteMetadata.alternativeDescription}
                big={true}
                buttons={[
                    ["Blog", "/blog"],
                    ["Projects", "/projects"],
                ]}
            />
            <div className="bg-dark text-light">
                <div className="container py-4">
                    <div className="row py-4">
                        <div className="col-12">
                            <h3>Hello and welcome to my website</h3>
                            <p>
                                Here you will be able to view a wonderful selection of my projects and have a read
                                through some of my blog posts where I will talk about a variety of topics including
                                software development, recent technological advancements and also some of my own life
                                stories (because people want to know about that).
                            </p>
                            <p>I hope that you enjoy your time here!</p>
                        </div>
                    </div>
                </div>
            </div>

            <div className="">
                <div className="container py-4">
                    <div className="row py-4">
                        <div className="col-12">
                            <h3>Projects</h3>
                            <p>Here is a list of my projects, basically my portfolio.</p>

                            <div className="card-group py-1">
                                {data.latestProjects.edges.map(edge => {
                                    const project = edge.node.frontmatter

                                    return (
                                        <div className="card" key={project.slug}>
                                            <h5 className="card-header">{project.title}</h5>
                                            <Img
                                                className="card-img-top"
                                                fluid={project.thumbnail.childImageSharp.fluid}
                                            />
                                            <div className="card-body">
                                                <p className="card-text">{project.description}</p>
                                            </div>
                                            <div className="card-footer">
                                                <Link className="stretched-link" to={project.slug}>
                                                    Visit project page
                                                </Link>
                                            </div>
                                        </div>
                                    )
                                })}
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="bg-light">
                <div className="container py-4">
                    <div className="row py-4">
                        <div class="col-12">
                            <h3 className="mb-3">Recent Posts</h3>
                        </div>
                        <div className="col-sm-12 col-md-8">
                            {data.latestPosts.edges.map(edge => {
                                const post = edge.node.frontmatter
                                return (
                                    <div className="col-12 mb-4 p-0">
                                        <div class="card">
                                            <Img
                                                className="card-img-top rounded"
                                                fluid={post.thumbnail.childImageSharp.fluid}
                                            />
                                            <div class="card-body">
                                                <h5 class="card-title">{post.title}</h5>
                                                <p class="card-text"></p>
                                                <p>{post.description}</p>
                                                <p></p>
                                                <Link class="stretched-link" to={post.slug}>
                                                    Continue Reading
                                                </Link>
                                            </div>
                                            <div class="card-footer">
                                                <p class="card-text text-muted">
                                                    <small>by {post.author}</small>
                                                    <span> - </span>
                                                    <small>
                                                        Posted <RelativeTime value={post.date} />
                                                    </small>
                                                </p>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })}

                            <div className="col-12 p-0">
                                <Link className="btn btn-secondary btn-block mb-4" to="/blog">
                                    View more
                                </Link>
                            </div>
                        </div>
                        <div className="col-sm-12 col-md-4">
                            <div class="list-group">
                                <dic class="list-group-item list-group-item-secondary">
                                    <p class="m-0">Social Media</p>
                                </dic>
                                <a
                                    class="list-group-item list-group-item-action d-flex align-items-center"
                                    href="https://www.facebook.com/profile.php?id=100009434476455"
                                >
                                    <FaFacebook fontSize="32px" />
                                    <span class="pl-3">Facebook</span>
                                </a>
                                <a
                                    class="list-group-item list-group-item-action d-flex align-items-center"
                                    href="https://twitter.com/wsngamerz"
                                >
                                    <FaTwitter fontSize="32px" />
                                    <span class="pl-3">Twitter</span>
                                </a>
                                <a
                                    class="list-group-item list-group-item-action d-flex align-items-center"
                                    href="https://www.instagram.com/wneild5"
                                >
                                    <FaInstagram fontSize="32px" />
                                    <span class="pl-3">Instagram</span>
                                </a>
                                <a
                                    class="list-group-item list-group-item-action d-flex align-items-center"
                                    href="https://github.com/wsngamerz"
                                >
                                    <FaGithub fontSize="32px" />
                                    <span class="pl-3">GitHub</span>
                                </a>
                                <a
                                    class="list-group-item list-group-item-action d-flex align-items-center"
                                    href="https://www.linkedin.com/in/wneild5"
                                >
                                    <FaLinkedin fontSize="32px" />
                                    <span class="pl-3">Linkedin</span>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default IndexPage

export const query = graphql`
    query HomePageQuery {
        site {
            siteMetadata {
                title
                description
                alternativeDescriptions
            }
        }
        latestPosts: allMarkdownRemark(
            filter: { frontmatter: { slug: { regex: "/blog/" } } }
            sort: { order: DESC, fields: [frontmatter___date] }
            limit: 5
        ) {
            edges {
                node {
                    frontmatter {
                        date(formatString: "MMMM DD, YYYY")
                        slug
                        title
                        description
                        author
                        thumbnail {
                            childImageSharp {
                                fluid(maxWidth: 1200) {
                                    ...GatsbyImageSharpFluid
                                }
                            }
                        }
                    }
                }
            }
        }
        latestProjects: allMarkdownRemark(filter: { frontmatter: { slug: { regex: "/projects/" } } }, limit: 5) {
            edges {
                node {
                    frontmatter {
                        slug
                        title
                        description
                        thumbnail {
                            childImageSharp {
                                fluid(maxWidth: 1200) {
                                    ...GatsbyImageSharpFluid
                                }
                            }
                        }
                    }
                }
            }
        }
    }
`
